module.exports = "<template>\n<div class=\"demo-step-container\">\n  <mu-stepper :activeStep=\"activeStep\">\n    <mu-step>\n      <mu-step-label>\n        选择活动地点\n      </mu-step-label>\n    </mu-step>\n    <mu-step>\n      <mu-step-label>\n        创建一个群组\n      </mu-step-label>\n    </mu-step>\n    <mu-step>\n      <mu-step-label>\n        宣传活动\n      </mu-step-label>\n    </mu-step>\n  </mu-stepper>\n  <div class=\"demo-step-content\">\n    <p v-if=\"finished\">\n      都完成啦!<a href=\"javascript:;\" @click=\"reset\">点这里</a>可以重置\n    </p>\n    <template v-if=\"!finished\">\n      <p>\n        {{content}}\n      </p>\n      <div>\n        <mu-flat-button class=\"demo-step-button\" label=\"上一步\" :disabled=\"activeStep === 0\" @click=\"handlePrev\"/>\n        <mu-raised-button class=\"demo-step-button\" :label=\"activeStep === 2 ? '完成' : '下一步'\" primary @click=\"handleNext\"/>\n      </div>\n    </template>\n  </div>\n</div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      activeStep: 0\n    }\n  },\n  computed: {\n    content () {\n      let message = ''\n      switch (this.activeStep) {\n        case 0:\n          message = '选择一个活动的地点'\n          break\n        case 1:\n          message = '创建群组，50人左右'\n          break\n        case 2:\n          message = '多在群里发消息宣传宣传'\n          break\n        default:\n          message = 'fuck! 又 TM 出错了！！！'\n          break\n      }\n      return message\n    },\n    finished () {\n      return this.activeStep > 2\n    }\n  },\n  methods: {\n    handleNext () {\n      this.activeStep++\n    },\n    handlePrev () {\n      this.activeStep--\n    },\n    reset () {\n      this.activeStep = 0\n    }\n  }\n}\n</script>\n\n<style>\n.demo-step-container {\n  width: 100%;\n  max-width: 700px;\n  margin: auto;\n}\n\n.demo-step-content {\n  margin: 0  16px;\n}\n\n.demo-step-button {\n  margin-top: 12px;\n  margin-right: 12px;\n}\n</style>\n"


